<template>
  <div class="main">
    <!-- {{this.message}} -->
    <!-- {{this.division}}, {{this.department}} -->
     <!-- Basic Rent allowance: {{this.rentAllowance}}, Basic Travel allowance: {{this.travelAllowance}} -->
    <b-row class="mt-1">
        <h2 class="page-head">Add/Edit Salary</h2>
        <b-col class="mt-3"  >
          <b-button class="mr-1 float-right button-style" variant="outline-primary" @click="lastRecord()"
            ><i class="fa fa-angle-double-right"></i
          ></b-button>
          <b-button class="mr-1 float-right button-style" variant="outline-primary" @click="nextRecord()"
            ><i class="fa fa-angle-right"></i
          ></b-button>
           <b-button
            class="mr-1 float-right button-style"
            variant="outline-primary"
            @click="previousRecord()"
            ><i class="fa fa-angle-left"></i
          ></b-button>
              <b-button
            class="mr-1 float-right button-style"
            variant="outline-primary"
            @click="firstRecord()"
            ><i class="fa fa-angle-double-left"></i
          ></b-button>
        </b-col>
              <!-- <b-col>
            <b-button
              variant="success"
              size="sm"
              class="float-right button-style"
              @click="refreshData"
              >Refresh</b-button
            >
          </b-col> -->
    </b-row>
    <hr>
    <b-row class="mt-1">
      <b-col cols="1" class="col-left">Month</b-col>
      <b-col cols="1" class="col-left">
        {{ this.employeeMET.monthName }}
      </b-col>
      <b-col cols="1" offset="1" class="col-left">Year</b-col>
      <b-col cols="1" class="col-left">
        {{ this.employeeMET.trnYear }}
      </b-col>
    </b-row>
    <b-row class="mt-1">
      <b-col cols="1" class="col-left">Employee</b-col>
      <b-col cols="1">
        <b-form-group>
          <b-input-group>
            <b-form-input
              class="col-right form-control form-control-sm"
              v-model="employeeMET.id"
              placeholder=""
              @change="getEmployee"
              v-b-tooltip.hover
              title="Enter employee code"
              tabindex="1"
            ></b-form-input>
            <template v-slot:append>
              <searchSm
                :column1Key="searchEmp.attr1.name"
                :column1label="searchEmp.attr1.label"
                :column2Key="searchEmp.attr2.name"
                :column2label="searchEmp.attr2.label"
                :fetchItem="fetchemployeeMET"
                @fetchD="fetchEmployeeList"
                :items="employeeList"
                :reset="defaultState"
                :totalRowsT="totalRows"
              />
            </template>
          </b-input-group>
        </b-form-group>
      </b-col>
      <b-col cols="1" offset="1" class="col-left">Name</b-col>
      <b-col cols="2" class="col-left">
        {{ this.employeeMET.empName }}
      </b-col>
      <b-col cols="1" class="col-left"> Department: </b-col>
      <b-col cols="2" class="col-left">
        {{ this.department.departmentName }}
      </b-col>
       <b-col cols="1" class="col-left"> Division: </b-col>
      <b-col cols="2" class="col-left">
        {{ this.division.divisionName }}
      </b-col>
    </b-row>
    <b-row class="mt-0">
      <b-col cols="6">
        <b-row class="mt-1">
          <b-col cols="3" class="col-left">Basic Salary</b-col>
          <b-col cols="2" class="col-right">
            {{ this.employeeMET.basicSalary | formatter }}
          </b-col>
        </b-row>
        <b-row class="mt-1">
          <b-col cols="3" class="col-left">Attendance</b-col>
          <b-col cols="2">
            <b-form-input
              class="col-right form-control form-control-sm"
              v-model="employeeMET.attendance"
              type="number"
              placeholder
              tabindex="2"
               @change="test"
            ></b-form-input>
          </b-col>
          <b-col cols="3">
            <b-form-text class="col-right form-control form-control-sm">
              {{ salary }}
            </b-form-text>
          </b-col>
        </b-row>
        <b-row class="mt-1">
          <b-col cols="3" class="col-left">Ot1</b-col>
          <b-col cols="2">
            <b-form-input
              class="col-right form-control form-control-sm"
              v-model="employeeMET.ot1"
              type="number"
              tabindex="3"
            ></b-form-input>
          </b-col>
          <b-col cols="3">
            <b-form-text class="col-right form-control form-control-sm">
              {{ ot1Amount }}
            </b-form-text>
          </b-col>
        </b-row>
        <b-row class="mt-1">
          <b-col cols="3" class="col-left">Ot2</b-col>
          <b-col cols="2">
            <b-form-input
              class="col-right form-control form-control-sm"
              v-model="employeeMET.ot2"
              type="number"
              tabindex="4"
            ></b-form-input>
          </b-col>
          <b-col cols="3">
            <b-form-text class="col-right form-control form-control-sm">
              {{ ot2Amount }}
            </b-form-text>
          </b-col>
        </b-row>
        <b-row class="mt-1">
          <b-col cols="3" class="col-left">Night Shift</b-col>
          <b-col cols="2">
            <b-form-input
              class="col-right form-control form-control-sm"
              v-model="employeeMET.nShiftAllowance"
              type="number"
              tabindex="5"
            ></b-form-input>
          </b-col>
          <b-col cols="3">
            <b-form-text class="col-right form-control form-control-sm">
              {{ nsAmount }}
            </b-form-text>
          </b-col>
        </b-row>

        <b-row class="mt-1">
          <b-col cols="3" class="col-left">Rent Allowance</b-col>
          <b-col cols="3" offset="2">
            <vue-numeric
              class="
                form-control form-control-sm
                styleLine
                col-right
                group1-color
              "
              style="padding-left: 0px"
              tabindex="6"
              placeholder
              :minus="true"
              separator=","
              :precision="3"
              v-model="employeeMET.rentAllowance"
            ></vue-numeric>
          </b-col>
        </b-row>
        <b-row class="mt-1">
          <b-col cols="3" class="col-left">Travel Allowance</b-col>
          <b-col cols="3" offset="2">
            <vue-numeric
              class="
                form-control form-control-sm
                styleLine
                col-right
                group1-color
              "
              style="padding-left: 0px"
              tabindex="7"
              placeholder
              :minus="true"
              separator=","
              :precision="3"
              v-model="employeeMET.travelAllowance"
            ></vue-numeric>
          </b-col>
        </b-row>
        <b-row class="mt-1">
          <b-col cols="3" class="col-left">Special Allowance</b-col>
          <b-col cols="3" offset="2">
            <vue-numeric
              class="
                form-control form-control-sm
                styleLine
                col-right
                group1-color
              "
              style="padding-left: 0px"
              tabindex="8"
              placeholder
              :minus="true"
              separator=","
              :precision="3"
              v-model="employeeMET.spageAllowance"
            ></vue-numeric>
          </b-col>
        </b-row>
      </b-col>
      <b-col cols="6">
        <b-row class="mt-1">
          <b-col cols="3" class="col-left">Loan deduction</b-col>
          <b-col cols="3" offset="2">
            <vue-numeric
              class="
                form-control form-control-sm
                styleLine
                col-right
                group1-color
              "
              style="padding-left: 0px"
              tabindex="9"
              placeholder
              :minus="true"
              separator=","
              :precision="3"
              v-model="employeeMET.loanDeduction"
            ></vue-numeric>
          </b-col>
          <b-button @click="toggleLoanInfo()" variant="link" size="sm">
            <i class="fas fa-info">Loan information</i>
          </b-button>
        </b-row>
        <b-row class="mt-1">
          <b-col cols="3" class="col-left">Eskan Bank loan</b-col>
          <b-col cols="3" offset="2">
            <vue-numeric
              class="
                form-control form-control-sm
                styleLine
                col-right
                group1-color
              "
              style="padding-left: 0px"
              tabindex="10"
              placeholder
              :minus="true"
              separator=","
              :precision="3"
              v-model="employeeMET.hBankLoan"
            ></vue-numeric>
          </b-col>
        </b-row>
        <b-row class="mt-1">
          <b-col cols="3" class="col-left">Gosi Amount</b-col>
          <b-col cols="3" offset="2">
            <vue-numeric
              class="
                form-control form-control-sm
                styleLine
                col-right
                group1-color
              "
              style="padding-left: 0px"
              tabindex="11"
              placeholder
              :minus="true"
              separator=","
              :precision="3"
              v-model="employeeMET.gosiAmount"
            ></vue-numeric>
          </b-col>
        </b-row>
        <b-row class="mt-1">
          <b-col cols="3" class="col-left">Late hours</b-col>
          <b-col cols="2">
            <b-form-input
              class="col-left form-control form-control-sm"
              v-model="employeeMET.lateHours"
              placeholder
              tabindex="12"
            ></b-form-input>
          </b-col>
          <b-col cols="3">
            <b-form-text class="col-right form-control form-control-sm">
              {{ lateHoursAmount | formatter }}
            </b-form-text>
          </b-col>
        </b-row>
        <div class="text-info" v-show="showLoanInfo">
          <Hr />
          <b-row class="mt-1">
            <b-col cols="3" class="col-left text-info">Loans Balance</b-col>
            <b-col cols="2" class="col-right">
              {{
                (empLoanSummary.drAmountTotal - empLoanSummary.crAmountTotal)
                  | formatter
              }}
            </b-col>
          </b-row>
          <b-row class="mt-1">
            <b-col cols="3" class="col-left">Loan Installment</b-col>
            <b-col cols="2" class="col-right">
              {{ empLoanSummary.loanInstallment | formatter }}
            </b-col>
          </b-row>
          <b-row class="mt-1">
            <b-col cols="3" class="col-left">Last transaction </b-col>
            <b-col cols="2" class="col-right">
              {{ empLoanSummary.lastTrnDate | formatDate }}
            </b-col>
          </b-row>
        </div>
        <br /><br /><br />
        <p v-show="netSalary > 0">
          <span class="w3-tag"
            ><h6>Salary payable: {{ netSalary }}</h6></span
          >
        </p>
      </b-col>
    </b-row>

    <!-- <hr /> -->
    <b-row class="mt-1">
      <b-col cols="6" class="col-left" tabindex="13">
        <MonthEndTransactionAllowance
          ref="mtaRef"
          :id="this.employeeMET.id"
          :trnYear="this.employeeMET.trnYear"
          :trnMonth="this.employeeMET.trnMonth"
          @allowance="getAllowance"
        />
      </b-col>
      <b-col cols="6" class="col-left" tabindex="16">
        <MonthEndTransactionDeduction
          ref="mtdRef"
          :id="this.employeeMET.id"
          :trnYear="this.employeeMET.trnYear"
          :trnMonth="this.employeeMET.trnMonth"
          @deduction="getDeduction"
        />
      </b-col>
    </b-row>
    <hr />
    <b-row class="mt-3" align-h="center">
      <b-col cols="3" offset="1">
        <b-button class="mr-1" variant="dark" @click="submit()" tabindex="19"
          >Submit</b-button
        >
        <b-button class="mr-1" variant="outline-secondary" @click="clearForm()" tabindex="20"
          >Clear</b-button
        >
        <b-button
          variant="secondary"
          class="mr-1"
          @click="monthEndTransactionDisplay()" tabindex="21"
          >Close</b-button
        >
      </b-col>
    </b-row>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import VueNumeric from 'vue-numeric'
import MonthEndTransactionAllowance from './MonthEndTransactionAllowance.vue'
import MonthEndTransactionDeduction from './MonthEndTransactionDeduction.vue'
import searchSm from '../components/SearchSm.vue'
export default {
  props: {
    setDisplay: { type: Function },
    currentMonth: {}
  },
  data () {
    return {
      showLoanInfo: false,
      loanDetails: {},
      loanInfo: false,
      loanAmount: 0,
      loanInstallment: 0,
      otRates: {},
      otherAllowance: 0,
      otherDeduction: 0,
      deductions: [],
      allowances: [],
      searchEmp: {
        attr1: { name: 'id', label: 'Employee Code' },
        attr2: { name: 'empName', label: 'Employee Name' }
      },
      rentAllowance: 0,
      travelAllowance: 0,
      basicAllowance: {},
      division: {},
      department: {},
      empployeeInfo: {}
    }
  },
  mounted () {
    this.loadOTRates()
    // console.log('mounted month end transactions')
  },
  components: {
    MonthEndTransactionAllowance,
    MonthEndTransactionDeduction,
    searchSm,
    VueNumeric
    // LoanSummary
  },
  computed: {
    ...mapGetters([
      'totalRows',
      'employeeMET',
      'showMETform',
      'message',
      'emplist',
      'employeeList',
      'fetchStartRec',
      'empLoanSummary',
      'metAllowance',
      'metDeduction'
    ]),
    netSalary () {
      return (
        parseFloat(this.salary) +
        parseFloat(this.ot1Amount) +
        parseFloat(this.ot2Amount) +
        parseFloat(this.nsAmount) +
        parseFloat(this.employeeMET.rentAllowance) +
        parseFloat(this.employeeMET.travelAllowance) +
        parseFloat(this.employeeMET.spageAllowance) -
        parseFloat(this.employeeMET.loanDeduction) -
        parseFloat(this.employeeMET.hBankLoan) -
        parseFloat(this.employeeMET.gosiAmount) -
        parseFloat(this.lateHoursAmount) +
        parseFloat(this.otherAllowance) -
        parseFloat(this.otherDeduction)
      ).toFixed(3)
    },
    salary: function () {
      return (
        (parseFloat(this.employeeMET.attendance || 0) *
          parseFloat(this.employeeMET.basicSalary || 0)) /
        30
      ).toFixed(3)
    },
    ot1Amount: function () {
      return (
        (parseFloat(this.employeeMET.ot1 || 0) *
          parseFloat(this.otRates.otRate1 || 0) *
          parseFloat(this.employeeMET.basicSalary)) /
        30 /
        8
      ).toFixed(3)
    },
    ot2Amount: function () {
      return (
        (parseFloat(this.employeeMET.ot2 || 0) *
          parseFloat(this.otRates.otRate2 || 0) *
          parseFloat(this.employeeMET.basicSalary)) /
        30 /
        8
      ).toFixed(3)
    },
    nsAmount: function () {
      return (
        parseFloat(this.employeeMET.nShiftAllowance || 0) *
        parseFloat(this.otRates.nightShiftAmount || 0)
      ).toFixed(3)
    },
    lateHoursAmount: function () {
      return (
        (parseFloat(this.employeeMET.lateHours || 0) *
          parseFloat(this.employeeMET.basicSalary || 0)) /
        240
      ).toFixed(3)
    }
  },
  filters: {
    formatter: function (value) {
      if (isNaN(value)) {
        return 0
      } else {
        return new Intl.NumberFormat(undefined, {
          minimumFractionDigits: 3
        }).format(value)
      }
    },
    formatValue: function (value) {
      if (isNaN(value)) {
        return 0
      } else {
        return value
      }
    },
    // bing ai
    formatNumber: function (num) {
      if (typeof num === 'number') {
        return num.toFixed(3)
      } else {
        return 0
      }
    }
  },
  methods: {
    ...mapActions([
      'METformDisplay',
      'fetchemployeeMET',
      'defaultState',
      'fetchEmployeeList',
      'fetchEmployeeLoanInfo',
      'saveEmployeeMET',
      'monthEndTransactionDisplay',
      'updateEmployeeMetID',
      'updateAllowance'
    ]),
    async test () {
      console.log('change in attendance')
      await this.getEmployeeBasicAllowance(this.employeeMET.id)
      this.rentAllowance = this.basicAllowance.rentAllowance
      this.travelAllowance = this.basicAllowance.travelAllowance
      let rentAllowanceT = 0
      console.log(this.rentAllowance)
      if (this.rentAllowance > 0) {
        rentAllowanceT = (this.rentAllowance / 30) * this.employeeMET.attendance
        console.log(this.rentAllowance)
        console.log(this.employeeMET.attendance)
        console.log(rentAllowanceT)
      }
      let travelAllowanceT = 0
      if (this.travelAllowance > 0) {
        travelAllowanceT = (this.travelAllowance / 30) * this.employeeMET.attendance
      }
      const allowanceData = { rentAllowanceT, travelAllowanceT }
      this.updateAllowance(allowanceData)
    },
    toggleLoanInfo () {
      this.showLoanInfo = !this.showLoanInfo
    },
    async getEmpInfo (id) {
      this.empployeeInfo = {}
      const url = process.env.VUE_APP_API_URL + `/employee/${id}`

      try {
        const response = await fetch(url)

        if (!response.ok) {
          throw new Error('Network response was not ok')
        }

        const data = await response.json()
        this.empployeeInfo = data
      } catch (error) {
        console.error(error)
      }
    },
    async getDivision (id) {
      this.divisionName = {}
      const url = process.env.VUE_APP_API_URL + `/division/${id}`

      try {
        const response = await fetch(url)

        if (!response.ok) {
          throw new Error('Network response was not ok')
        }

        const data = await response.json()
        this.division = data
      } catch (error) {
        console.error(error)
      }
    },
    async getDepartment (id) {
      this.departmentName = {}
      const url = process.env.VUE_APP_API_URL + `/department/${id}`

      try {
        const response = await fetch(url)

        if (!response.ok) {
          throw new Error('Network response was not ok')
        }

        const data = await response.json()
        this.department = data
      } catch (error) {
        console.error(error)
      }
    },
    async getEmployeeBasicAllowance (id) {
      this.basicAllowance = {}
      const url = process.env.VUE_APP_API_URL + `/employee/allowance/${id}`

      try {
        const response = await fetch(url)

        if (!response.ok) {
          throw new Error('Network response was not ok')
        }

        const data = await response.json()
        this.basicAllowance = data
        console.log('basic allowance from db')
        console.log(this.basicAllowance)
        console.log('travel allowaince' + this.basicAllowance.travelAllowance)
      } catch (error) {
        console.error(error)
      }
    },
    async firstRecord () {
      let i = 0
      const url = process.env.VUE_APP_API_URL + '/salaryFirstRecord'
      fetch(url)
        .then((res) => res.json())
        .then((data) => {
          i = data
          console.log(i)
        })
        .then(() => {
          this.getEmployeeRecord(i)
        })
        .then(() => {
          this.updateEmployeeMetID(i)
        })
        .catch((err) => console.error(err))
    },
    async lastRecord () {
      let i = 0
      const url = process.env.VUE_APP_API_URL + '/salaryLastRecord'
      fetch(url)
        .then((res) => res.json())
        .then((data) => {
          i = data
          console.log(i)
        })
        .then(() => {
          this.updateEmployeeMetID(i)
        })
        .then(() => {
          this.getEmployeeRecord(i)
        })
        .catch((err) => console.error(err))
    },
    async previousRecord () {
      let i = 0
      const url =
        process.env.VUE_APP_API_URL +
        `/salaryPreviousRecord/${this.employeeMET.id}`
      fetch(url)
        .then((res) => res.json())
        .then((data) => {
          i = data
          console.log(i)
        })
        .then(() => {
          this.updateEmployeeMetID(i)
        })
        .then(() => {
          this.getEmployeeRecord(i)
        })
        .catch((err) => console.error(err))
    },
    async nextRecord () {
      let i = 0
      const url =
        process.env.VUE_APP_API_URL +
        `/salaryNextRecord/${this.employeeMET.id}`
      fetch(url)
        .then((res) => res.json())
        .then((data) => {
          i = data
          console.log(i)
        })
        .then(() => {
          this.updateEmployeeMetID(i)
        })
        .then(() => {
          this.getEmployeeRecord(i)
        })
        .catch((err) => console.error(err))
    },
    getAllowance: function (x) {
      console.log('getAllowance:' + x)
      this.allowances = x
      this.otherAllowance = this.otherAllowanceGTotal()
    },
    getDeduction: function (x) {
      this.deductions = x
      this.otherDeduction = this.otherDeductionGTotal()
    },
    async getEmployee () {
      await this.getEmployeeRecord(this.employeeMET.id)
      await this.getEmpInfo(this.employeeMET.id)
      await this.getDepartment(this.empployeeInfo.deptCode)
      await this.getDivision(this.department.divisionCode)
    },
    async getEmployeeRecord (x) {
      this.rentAllowance = 0
      this.travelAllowance = 0
      await this.fetchemployeeMET(x)
      this.rentAllowance = this.employeeMET.rentAllowance
      this.travelAllowance = this.employeeMET.travelAllowance
      this.attendance = this.employeeMET.attendance
      await this.fetchEmployeeLoanInfo(x)
      console.log('fetch employee')
      if (this.message.length !== 0) {
        this.$toasted.show(this.message, {
          theme: 'bubble',
          position: 'top-right',
          duration: 5000
        })
      }
    },
    async submit () {
      // console.log(JSON.stringify(this.allowances))
      // console.log(JSON.stringify(this.deductions))
      // alert('save')
      this.employeeMET.otherAllowances = this.allowances
      this.employeeMET.otherDeductions = this.deductions

      this.employeeMET.otherAllowanceTotal = this.otherAllowanceGTotal()
      this.employeeMET.otherDeductionTotal = this.otherDeductionGTotal()
      console.log(
        'other allowance total:' + this.employeeMET.otherAllowanceTotal
      )
      console.log(
        'other deduction total:' + this.employeeMET.otherDeductionTotal
      )
      await this.saveEmployeeMET(this.employeeMET)
      if (this.message.length !== 0) {
        this.$toasted.show(this.message, {
          theme: 'bubble',
          position: 'top-right',
          duration: 5000
        })
      }
    },
    // otherAllowanceGTotal () {
    //   return this.allowances.reduce(function (a, c) {
    //     return a + Number(c.otherAllowance || 0)
    //   }, 0)
    // },
    otherAllowanceGTotal () {
      let total = 0
      try {
        total = this.allowances.reduce(function (a, c) {
          return a + Number(c.otherAllowance || 0)
        }, 0)
      } catch (error) {
        // console.error(error)
      }
      return total
    },
    // otherDeductionGTotal () {
    //   return this.deductions.reduce(function (a, c) {
    //     return a + Number(c.otherDeduction || 0)
    //   }, 0)
    // },
    otherDeductionGTotal () {
      let total = 0
      try {
        total = this.deductions.reduce(function (a, c) {
          return a + Number(c.otherDeduction || 0)
        }, 0)
      } catch (error) {
        // console.error(error)
      }
      return total
    },
    async clearForm () {
      console.log('clear Form')
      await this.defaultState()
      this.rentAllowance = 0
      this.travelAllowance = 0
      // this.getAllowance([{ description: '', otherAllowance: 0 }])
      await this.$refs.mtaRef.RemoveItemRow()
      await this.$refs.mtdRef.RemoveItemRow()
    },
    async loadOTRates () {
      const url = process.env.VUE_APP_API_URL + '/rates'
      const response = await fetch(url)
      this.otRates = await response.json()
      // console.log(this.otRates)
    }
  }
  //    watch: {
